.header-color{
    background: #000000;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #434343, #000000);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #434343, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid{
    background: #000000;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #434343, #000000);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #434343, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
text-align: center;
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
}
.projects-grid{
    display:flex;
}

.avatar-img{
    height: 250px;
    padding-top: 5em;
}
.banner-text{
    opacity: .8;
    width: 75%;
    margin: auto;
    border-radius: 15px;
}

.banner-text h1{
    font-size: 66px;
    font-weight: bold;
    color: wheat;
}
.banner-text hr{
    border-top: 5px dotted wheat;
    width: 50%;
    margin: auto;
}
.banner-text p{
    color: wheat;
    font-size: 20px;
    padding:1em;
}
.social-links{
    display: flex;
    justify-content: space-between;
    width: 15%;
    margin: auto;
}
.social-links i{
    color: wheat;
    font-size: 5em;
    padding: 8px;
}

.contact-body{
    margin:auto;
    background: grey;
    position: fixed;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: 2em;
}

.contact-grid{
    text-align: center;
    justify-content: center;
    width: 80%;
    height: 550px;
    background: white;
    color: black;
    border: 2px solid red;
    margin-bottom: 20px;
}
.contact-grid h2{
    font-family: "anton", sans-serif;
}
.contact-grid p{
    font-family: "oxygen", sans-serif;
    font-weight: bold;
}
.contact-grid hr {
    border-top: 5px dotted black;
    width: 50%;
    margin: auto;
}
.contact-list i {
    font-size: 66px;
    padding-right: 5rem;
}

.resume-right-col{
    background: #27221f;
    color: white;
    padding:2rem
}